        <img
          src="../../assets/images/system-tooltip/arrow-list.svg"
          alt="arrow"
          class="arrow"
          v-on:click="visible = !visible"
        />
        <p class="title">{{ tip.title }}</p>
        <p class="fs-medium">{{ tip.category }}</p>
      </div>
      <div class="tip-container" v-show="visible">
        <img
          src="../../assets/images/system-tooltip/arrow-list.svg"
          alt="arrow"
          class="arrow rotate-arrow"
          v-on:click="visible = !visible"
        />
        <p class="title">{{ tip.title }}</p>
        <p class="des">Категория: {{ tip.category }}</p>
        <p class="fs-medium">{{ tip.description }}</p>
      </div>
      <div class="date">
        <div class="dtop">{{ tip.created_at }}</div>
        <!-- <div class="dbot">{{ tip.date.time }}</div> -->
      </div>
      <button class="rem" @click="CHANGE_RECENT(tip.id)">
        <img
          src="@/assets/images/system-tooltip/remove-tip.svg"
          alt="remove tip"
        />
      </button>
    </div>
  </li>
</template>

<script>
import { mapMutations } from "vuex";
export default {
  props: ["tip"],
  data() {
    return {
      visible: false
    };
  },
  computed: {},
  methods: {
    ...mapMutations("Tip", ["CHANGE_RECENT", "CHANGE_DONE", "CHANGE_REMOVED"])
  },
  mounted() {
    // console.log(this.tip);
  }
};
</script>

