<template>
  <div class="main-container">
    <div class="btn-container" @click="isChecked">
      <label class="mr-1">
        <input
          type="radio"
          class="radio js-recent"
          name="name"
          :checked="isCheckRecent"
        />
        <div class="pic">
          <span class="text">Recent</span>
        </div>
      </label>

      <label class="mr-1">
        <input
          type="radio"
          class="radio js-done"
          name="name"
          :checked="isCheckDone"
        />
        <div class="pic">
          <span class="text">Done</span>
        </div>
      </label>

      <label>
        <input
          type="radio"
          class="radio js-removed"
          name="name"
          :checked="isCheckRemoved"
        />
        <div class="pic">
          <span class="text">Removed</span>
        </div>
      </label>
    </div>

    <div class="wrapTip">
      <template v-if="isCheckRecent">
        <TipElement :tips="recent" />
      </template>
      <template v-if="isCheckDone">
        <TipElement :tips="done" />
      </template>
      <template v-if="isCheckRemoved">
        <TipElement :tips="removed" />
      </template>
    </div>
  </div>
</template>

<script>
import TipElement from "@/components/tips/TipElement";
import { mapState, mapActions } from "vuex";
export default {
  name: "SystemTooltip",
  components: {
    TipElement
  },
  data() {
    return {
      isCheckRecent: true,
      isCheckDone: false,
      isCheckRemoved: false
    };
  },
  computed: {
    ...mapState("Tip", ["recent", "done", "removed"])
  },
  methods: {
    ...mapActions("Tip", ["GET_TIPS"]),
    isChecked(event) {
      this.isCheckRecent = false;
      this.isCheckDone = false;
      this.isCheckRemoved = false;

      const target = event.target;
      if (target.classList.contains("js-recent")) {
        return (this.isCheckRecent = true);
      }
      if (target.classList.contains("js-done")) {
        return (this.isCheckDone = true);
      }
      if (target.classList.contains("js-removed")) {
        return (this.isCheckRemoved = true);
      }
    }
  },
  beforeMount() {
    if (!this.recent.length) {
      this.GET_TIPS();
    }
  }
};
</script>

<style scoped>
.main-container {
  display: flex;
  margin: 1.3rem;
  flex-wrap: wrap;
  /* height: 100vh; */
}
.btn-container {
  width: 50vw;
  display: inline-flex;
}
.text {
  font-size: 2.7rem;
  color: rgba(255, 255, 255, 0.5);
}
.pic {
  display: inline-block;
  background-color: #262626;
  width: 15vw;
  height: 4rem;
  border-radius: 2.5rem;
  cursor: pointer;
  position: relative;
}
.pic:hover {
  background-color: #363636;
  transition: 0.2s;
}
.radio {
  display: none;
}
.pic::before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 15vw;
  height: 4rem;
  border-radius: 2.5rem;
  box-shadow: 0 0 0.5rem 0.1rem rgba(255, 255, 255, 0.5);
  opacity: 0;
  transition: 0.2s;
}
.radio:checked + .pic::before {
  opacity: 1;
  color: white;
}
.wrapTip {
  margin-top: 1.2rem;
}
.mr-1 {
  margin-right: 1.8rem;
}
</style>
